<template>
  <div id="Srore_div">
    <!-- <template slot-scope="props">
      <span>{{ props.row.id }}</span>
    </template> -->
    <h2>店铺管理
      <el-button type="primary"
                 @click="StoreSave">保存</el-button>
    </h2>
    <p>
      <label>店铺名称</label>
      <el-input v-model="name"
                clearable>
      </el-input>
    </p>
    <p>
      <label>店铺公告</label>
      <el-input type="textarea"
                :autosize="{ minRows: 8, maxRows: 16}"
                v-model="bulletin"
                clearable
                size="medium"
                style="width:50%">
      </el-input>
    </p>
    <p>
      <label>店铺头像</label>
      <el-upload class="avatar-uploader"
                 :action="SHOP_UP_IMG"
                 :show-file-list="false"
                 :on-success="shopavatarSuccess">
        <img v-if="avatar"
             :src="GET_IMG_store+avatar"
             class="avatar">
        <i v-else
           class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
    </p>
    <p id="SHOP_UP">
      <label>店铺图片</label>
      <el-upload :action="SHOP_UP_IMG"
                 list-type="picture-card"
                 :on-remove="screenShotRemove"
                 :on-success="screen_up"
                 :file-list="fileImgs"
                 :limit="4">
        <i class="el-icon-plus"></i>
      </el-upload>

    </p>

    <p>
      <label>起送价格</label>
      <el-input placeholder="起送价格"
                v-model="minPrice"
                clearable
                type="number">
      </el-input>
    <p>
      <label>配送费</label>
      <el-input placeholder="配送费"
                v-model="deliveryPrice"
                clearable
                type="number">
      </el-input>
    </p>
    <p>
      <label>送达时间</label>
      <el-input placeholder="送达时间"
                v-model="deliveryTime"
                clearable
                type="number">
      </el-input>
    </p>
    <p>
      <label>配送描述</label>
      <el-input placeholder="配送描述"
                v-model="description"
                clearable>
      </el-input>
    </p>
    <p>
      <label>店铺评分</label>
      <el-input placeholder="店铺评分"
                v-model="score"
                clearable
                type="number">
      </el-input>
    </p>
    <p>
      <label>销量</label>
      <el-input placeholder="销量"
                v-model="sellCount"
                clearable
                type="number">
      </el-input>
    </p>
    <!-- 活动 -->
    <p style="margin: 30px 0 30px 40px">
      <el-checkbox-group v-model="supports">
        <el-checkbox label="夏日大促全场饮品8折"></el-checkbox>
        <el-checkbox label="套餐限时5折"></el-checkbox>
        <el-checkbox label="满30-20"></el-checkbox>
        <el-checkbox label="充值100送100"></el-checkbox>
        <el-checkbox label="充值1000送500"></el-checkbox>
        <el-checkbox label="首单额外减5块"></el-checkbox>
      </el-checkbox-group>
    </p>
    <p class="block"
       style="padding-bottom:60px">
      <label class="demonstration">营业时间</label>
      <el-time-picker is-range
                      v-model="date"
                      range-separator="至"
                      start-placeholder="开始时间"
                      end-placeholder="结束时间"
                      placeholder="选择时间范围">
      </el-time-picker>
    </p>
  </div>
</template>

<script>
//shopedit
import { shopinfo, SHOP_UP_IMG, GET_IMG_store, shopedit } from '@/API/apis'
import { getchtime2 } from '@/utils/gettime'
export default {
  data () {
    return {
      id: "",
      name: "",
      bulletin: "",
      avatar: '',//商店头像
      pics: [],//店铺图片
      deliveryPrice: 0,
      deliveryTime: null,
      description: '',
      score: 0,
      sellCount: 0,
      supports: [],
      date: [],
      minPrice: 0,
      fileImgs: []//照片墙接收店铺图片
    }
  },
  created () {
    this.SHOP_UP_IMG = SHOP_UP_IMG
    this.GET_IMG_store = GET_IMG_store
    //获得服务器数据
    shopinfo().then(res => {
      for (const key in res.data.data) {
        this[key] = res.data.data[key]
      }
      //将this.pics中的数据.map 放入fileImgs中
      this.fileImgs = this.pics.map(items => {
        return {
          name: items,
          url: GET_IMG_store + items
        }
      })
    })
  },
  methods: {
    //店铺头像上传成功回调
    shopavatarSuccess (res) {
      this.avatar = res.imgUrl;
      if (res.code == 0) {
        this.$message({
          message: '店铺头像上传成功',
          type: 'success'
        });
      }
    },

    //店铺图片上传成功回调
    screen_up (res) {
      if (res.code == 0)
        this.pics.push(res.imgUrl);
    },
    // 移除图片回调
    screenShotRemove (res) {
      this.pics.splice(this.pics.indexOf(res.name), 1);
    },
    // 保存修改
    StoreSave () {
      this.date = [getchtime2(this.date[0]), getchtime2(this.date[1])]
      shopedit(this.id, this.name, this.bulletin, this.avatar, this.deliveryPrice, this.deliveryTime, this.description, this.score, this.sellCount, JSON.stringify(this.supports), JSON.stringify(this.date), JSON.stringify(this.pics)).then(res => {
        if (res.data.code == 0) {
          this.$message({
            message: '修改信息成功!!!',
            type: 'success'
          });
        }
      })
    }
  }

}
</script>
<style lang="less" scoped>
#Srore_div {
  height: 100%;
  .el-input {
    width: 33%;
  }
  h2 {
    margin-bottom: 10px;
  }
  p {
    display: flex;
    align-items: flex-start;
    padding: 10px;
    img {
      width: 145px;
      height: 145px;
    }
    label {
      text-align: right;
      width: 70px;
      margin-right: 20px;
      line-height: 40px;
    }
    .el-checkbox {
      width: 150px;
      text-align: left;
    }
  }
}
#SHOP_UP {
  img {
    width: 145px;
    height: 145px;
  }
}
</style>